import userobject from "../../../script/userobject";

export default {
  name: "buy",
  mixins: [userobject],
  props: {
    topic_data: {},
  },
  data() {
    return {
      buy_loading: false,
      buy_disable: true,
      buy_confirm: false,
    }
  },
  destroyed() {
    this.listener_loginout.off();
  },
  created() {
    console.log('vip_degree:', this.vip_degree);
    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.$nextTick(
        () => {
          this.$forceUpdate();
        }
      );
    });
  },
  computed: {
    vip_degree: function () {
      return this.this_vip_degree(this.$store.state.user.userinfo);
    },
    not_engout_gold: function () {
      try {
        return this.topic_data.buy_theav.price > this.$store.state.user.userinfo.gold;
      } catch (e) {

      }
      return false;
    },
    torrent_url: function () {
      var mktorrent = this.topic_data.mktorrent;
      if (mktorrent && mktorrent.torrent_id) {
        return "/torrent/download/" + mktorrent.torrent_id;
      }
    }
  },
  methods: {
    buy_event: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      this.buy_confirm = true;
    },
    on_click_file: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      this.buy_confirm = true;
    }
  }
}
